namespace pixi_picture {
    export function mapFilterBlendModesToPixi(array: Array<Array<PictureShader>> = []): Array<Array<PictureShader>>
    {
        //TODO - premultiply alpha would be different.
        //add a boolean for that!
        array[PIXI.BLEND_MODES.OVERLAY] = [new OverlayShader(0), new OverlayShader(1), new OverlayShader(2)];
        array[PIXI.BLEND_MODES.HARD_LIGHT] = [new HardLightShader(0), new HardLightShader(1), new HardLightShader(2)];
        array[PIXI.BLEND_MODES.SOFT_LIGHT] = [new SoftLightShader(0), new SoftLightShader(1), new SoftLightShader(2)];

        return array;
    }
}
